import React, { useState } from 'react';
import "./index.css";
import { Form, message, Button, Row, Col } from 'antd';
import Api from '../../helpers/api';

import {
    EyeOutlined,
    EyeInvisibleOutlined,
    ArrowLeftOutlined
} from '@ant-design/icons';

const api = new Api();

const Forgot = (props) => {
    const [form] = Form.useForm();
    const [errors, setErrors] = useState({})
    const [isLoading, setIsLoading] = useState(false)
    const [showPassword, setShowPassword] = useState(false)
    const [formData, setFormData] = useState({ password: "", password_confirmation: "" })

    const handleInput = (e, name) => {
        setFormData({ ...formData, [name]: e.target.value })
    }

    const handleShowPassword = (e) => {
        setShowPassword(!showPassword)
    }

    const onFinish = () => {
        const { code } = props.match.params

        setIsLoading(true)
        api.confirmEmail(code, formData)
            .then(response => {
                const { data } = response
                setIsLoading(false)
                message.success(data.message);
            })
            .catch(error => {
                setIsLoading(false)
                if (error.response && error.response.data) {
                    const { errors } = error.response.data
                    setErrors(errors)
                    message.error(error.response.data.message);
                }
            });
    };

    return (
        <div className="d-lg-flex half">
            <div className="bg order-1 order-md-2 login-form-wrapper" />
            <div className="contents order-2 order-md-1">
                <div className="container" style={{ backgroundColor: '#fff' }}>
                    <div className="row align-items-center justify-content-center">
                        <div className="col-md-7">
                            <h3>Create Password</h3>
                            <p className="mb-4">Create a new password for your account</p>

                            <Form form={form} onFinish={onFinish}>

                                <div className="form-group first">
                                    <label>New Password  <span style={{ color: 'red' }}>{errors && errors.password ? errors.password : null}</span></label>
                                    <Row>
                                        <Col md={22}>
                                            <input
                                                onChange={value => handleInput(value, 'password')}
                                                type={showPassword ? 'text' : 'password'}
                                                className="form-control" placeholder="New Password" />
                                        </Col>
                                        <Col md={2}>
                                            <Button
                                                onClick={handleShowPassword}
                                                type="link"
                                                icon={
                                                    showPassword ?
                                                        <EyeInvisibleOutlined style={{ color: 'black' }} /> :
                                                        <EyeOutlined style={{ color: 'black' }} />
                                                }
                                            />
                                        </Col>
                                    </Row>
                                </div>

                                <div className="form-group last mb-3">
                                    <label>Confirm Password  <span style={{ color: 'red' }}>{errors && errors.password ? errors.password : null}</span></label>
                                    <Row>
                                        <Col md={22}>
                                            <input
                                                onChange={value => handleInput(value, 'password_confirmation')}
                                                type={showPassword ? 'text' : 'password'}
                                                className="form-control" placeholder="Confirm Password" />
                                        </Col>
                                        <Col md={2}>
                                            <Button
                                                onClick={handleShowPassword}
                                                type="link"
                                                icon={
                                                    showPassword ?
                                                        <EyeInvisibleOutlined style={{ color: 'black' }} /> :
                                                        <EyeOutlined style={{ color: 'black' }} />
                                                }
                                            />
                                        </Col>
                                    </Row>
                                </div>

                                <Button className="form-button"
                                    size="large"
                                    block
                                    type="primary"
                                    htmlType="submit"
                                    loading={isLoading}>
                                    Create Password
                                </Button>

                                <Button
                                    onClick={() => { props.history.push('/login') }}
                                    className="form-button"
                                    icon={<ArrowLeftOutlined />}
                                    size="large" block>Back to Login</Button>
                            </Form>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Forgot;
